.title {
  color: var(--color-main, #050505);
  font-size: 18px;
  font-weight: 800;
  line-height: 1.33;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

@media screen and (min-width: 768px) {
  .list {
    gap: 20px;
  }
}
